import { useDispatch } from 'react-redux';
import { logOutUser } from 'redux/auth/auth-operations';
import { StyledBadge, Wrapper } from './UserMenu.styled';
import { useAuth } from 'hooks/useAuth';
import { Avatar, Chip } from '@mui/material';

export const UserMenu = () => {
  const dispatch = useDispatch();
  const { user } = useAuth();

  const handleLogOut = () => dispatch(logOutUser());

  return (
    <Wrapper>
      <p> {user.name}</p>

      <Chip
        avatar={
          <StyledBadge
            overlap="circular"
            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            variant="dot"
          >
            <Avatar
              alt="avatar"
              src={`https://api.dicebear.com/6.x/croodles/svg`}
              sx={{ width: 24, height: 24 }}
            />
          </StyledBadge>
        }
        label="LogOut"
        variant="outlined"
        onClick={handleLogOut}
        sx={{
          border: '1px solid #dfcece',
          color: '#000000',
          textShadow: '-1px -1px 1px #ffffff31, 1px 1px 1px #00000031',
          boxShadow: 3,
        }}
      />
    </Wrapper>
  );
};
//     React UserMenu,      
//    .

//    ,  , useDispatch  react - redux,  
//         Redux,    
// logOutUser  auth - operations    .

//  UserMenu    useAuth,     
//  ,   handleLogOut     "LogOut",    logOutUser   dispatch.

//   UserMenu   JSX,    ,
//       "LogOut".    Dicebear API,  
//      . "LogOut" 
//   ,    ,     .